import React, { FC } from 'react';
import { useSelector } from 'react-redux';
import {
  CellEditProps,
  ClickEnum,
  ColumnsProps,
  DefaultSortProps,
  FilterType,
  getExtraData,
  OrderType,
  SearchDataGrid,
  SearchViewProps,
  WindowComponent,
} from 'summer';
import { hasEmpresa } from '../../../../../dependencies/empresaDependency';
import { CLASS_NAME, Gia } from '../../../../../models/dtos/mlf/gia';
import { Reducers } from '../../../../../store/ducks';
import { GlobalState } from '../../../../../store/ducks/global.duck';

const defaultSorted: DefaultSortProps[] = [
  {
    dataField: 'referencia',
    order: OrderType.ASC,
  },
];

const cellEditProps: CellEditProps = {
  blurToSave: true,
  mode: ClickEnum.CLICK,
};

const GiaSearchView: FC<SearchViewProps<Gia>> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const columns: ColumnsProps[] = [
    {
      dataField: 'referencia',
      editable: false,
      filter: {
        type: FilterType.DATE,
      },
      sort: true,
      text: 'Referência',
    },
  ];
  return (
    <WindowComponent title="Manutenção de Gia" dependencies={[() => hasEmpresa(globalReducer.globalParameter)]} wikiId={39}>
      <SearchDataGrid
        {...props}
        {...props.searchDataGridProps}
        dtoClassName={CLASS_NAME}
        columns={columns}
        cellEditProps={cellEditProps}
        defaultSorted={defaultSorted}
        extraData={getExtraData(props, 'GiaEditView', CLASS_NAME)}
      />
    </WindowComponent>
  );
};

export default GiaSearchView;
